* {
  margin: 0;
  padding: 0; }

body {
  background-color: #fafafa;

  .bg-faded {
    background-color: white; }

  .navbar {
    border-bottom: 1px solid rgba(0, 0, 0, .0975);
    margin-bottom: 1.3rem;

    .navbar-brand:before {
      content: '#';
      color: #3897f0; }

    @media (max-width: 991px) {
      .container {
        padding-right: 0;
        padding-left: 0; } }

    @media (max-width: 576px) {
      .container {
        position: initial;
        margin-left: 0; } } }

  [data-name="card"] {
    margin-bottom: 1rem;
    .btn-block {
      margin-top: .5rem;
      white-space: inherit;
      transition: background-color .4s ease-out; }
    .nav.flex-column {
      width: 100%;
      .nav-link {
        padding-left: 1rem;
        padding-right: 1rem;
        padding-top: .05rem;
        padding-bottom: .1rem; } } }

  [data-loader] {
    margin-top: 1rem;
    margin-bottom: 1rem; } }



